.todoComponent{
	max-width: 90%;
	height: auto;
	display: flex;
	gap: 0.7rem;
	padding-bottom: 0.7rem;
	border-bottom: 1px solid #343a40;
	margin-left: 1.3rem;
}
.markTodoAsDone{
	background-color: transparent;
	border-radius: 50%;
	border: none;
}
.todoText {
	color: #ced4da;
	word-break: break-word;
	cursor: pointer;
	word-wrap: break-word;
	padding-left: 0.25rem;
	font-size: 1.2rem;
}
.checked{
	display: flex;
	flex: 1;
	background-color: #35a7ff;
	align-self: stretch;
}
@media only screen and (max-width: 945px){
	.todoText{
		font-size: 1rem;
	}
}